/* Estilos Gerais */
body {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
}

.about-us {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 200px;
  padding-right: 200px;
  color: #000000;
  text-shadow: 2px 2px 4px #ffdd57;
}

.about-us h2 {
  font-size: 2rem;
  color: #000000;
  text-align: center;
  margin-bottom: 10px;
}

.about-us h3 {
  font-size: 1.5rem;
  color: #000000;
  text-align: center;
  margin-bottom: 20px;
}

.about-us p {
  font-size: 1.5rem;
  line-height: 1.6;
  color: #000000;
  text-indent: 30px;
  text-align: justify;
  margin-bottom: 15px;
}

.about-us p:last-child {
  margin-bottom: 50px;
}

.titulo-equipe {
  width: auto;
  max-width: 600px;
  margin: 0 auto;
  align-items: center;
  text-align: center;
  padding: 10px 20px;
  text-transform: uppercase;
  border-radius: 8px;
  font-size: 17px;
  font-weight: 500;
  color: #00000080;
  text-shadow: 2px 2px 4px #ffdd57;
  background: transparent;
  cursor: pointer;
  box-shadow: transparent;
  border: 1px solid #ffffff80;
  transition: 0.5s ease;
  user-select: none;
}

.titulo-equipe h2 {
  color: #000;
}

.titulo-equipe:hover,
:focus {
  color: #000000;
  background: #ffdd57;
  border: 1px solid #ffdd57;
  text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 20px #ffffff;
  box-shadow: 0 0 5px #ffdd57, 0 0 20px #ffdd57, 0 0 50px #ffdd57,
    0 0 100px #ffdd57;
}

.desc-equipe {
  display: flex;
  gap: 50px;
  justify-content: center;
  padding: 10px;
  color: #000000;
  flex-wrap: wrap;
}

.adv-container {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  background-color: #282828;
  align-items: center;
  max-width: 350px;
  text-align: center;
  border-radius: 8px;
}

.nome-adv h3 {
  font-size: 1.5rem;
  color: #ffdd57;
  margin-top: 20px;
  padding: 0px;
}

.adv {
  display: flex;
  text-align: justify;
  background-color: #282828;
  font-size: 1.5rem;
  padding: 30px;
  height: 300px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  flex-direction: column;
}

.adv p {
  font-size: 1.2rem;
  color: #ffffff;
  line-height: 1.5;
  font-weight: 300;
}

@media (max-width: 768px) {
  .desc-equipe {
      flex-direction: column; /* Para dispositivos móveis, a ordem vira vertical */
      align-items: center;
  }
}

/* Título da seção */
.titulo-about-us {
  background-color: #000000;
  display: flex;
  align-items: center;
}

.titulo-about-us h1 {
  padding: 10px;
  margin-left: 20px;
  margin-bottom: 5px;
  color: #ffdd57;
}

/* Container para as imagens */
.image-container {
  display: flex;
  gap: 50px;
  justify-content: center;
  margin-top: 50px;
}

/* Cada item de imagem */
.image-item {
  position: relative;
  width: 350px;
  text-align: center;
}

/* Imagem */
.image-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: opacity 0.3s ease;
}

/* Descrição que inicialmente está oculta */
.description {
  position: absolute;
  bottom: -30px; /* Fica logo abaixo da imagem */
  left: 0;
  right: 0;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  font-size: 0.9rem;
  border-radius: 5px;
  opacity: 0; /* Torna a descrição invisível */
  transition: opacity 0.3s ease, bottom 0.3s ease;
}

/* Ao passar o mouse, mostra a descrição */
.image-item:hover .description {
  opacity: 1;
  bottom: 0; /* Move a descrição para o lugar certo */
}

/* Opcional: adicionar efeito de transição na imagem */
.image-item:hover img {
  opacity: 0.8; /* Faz a imagem ficar um pouco opaca quando o mouse passa */
}


/* Responsividade */
@media (max-width: 768px) {
  .about-us {
    width: 95%;
    padding: 15px;
  }

  .about-us h2 {
    font-size: 1.8rem;
  }

  .about-us h3 {
    font-size: 1.3rem;
  }

  .about-us p {
    font-size: 0.9rem;
  }
}